import React from 'react';
import { FaRegUser } from 'react-icons/fa';

interface Props {
  values: Value[];
}
interface Value {
  title: string;
  description: string;
}

const Icon = () => (
  <div className="value-icon-circle flex justify-center items-center">
    <FaRegUser className="text-grey text-3xl" />
  </div>
);

const ValueCard = ({ title, description }: Value) => (
  <div className="flex p-8">
    <Icon />
    <div className="text-left pl-4">
      <h3 className="text-blue">{title}</h3>
      <p>{description}</p>
    </div>
  </div>
);

const Values = ({ values }: Props) => (
  <div className="container p-8 md:mx-auto max-w-full">
    <h2 className="text-center font-brand text-blue">Our values</h2>
    <div className="pt-4 flex w-full justify-evenly flex-wrap lg:flex-nowrap">
      {values.map(value => (
        <ValueCard title={value.title} description={value.description} />
      ))}
    </div>
  </div>
);

export default Values;
